import Link from "next/link";

import { money, formatDate } from "@/lib/db-utils";

export const metadata = {
  title: "Payment Successful | Subscription Activated",
  description: "Your subscription payment was successful. Your affiliate account is now active and ready to use.",
  robots: {
    index: false,
    follow: false,
  },
};

async function getSuccessInfo(affiliate_id) {
  const base = process.env.APP_URL || "http://localhost:3000";

  const res = await fetch(`${base}/api/affiliate/payment-intent?affiliate_id=${affiliate_id}`,
    { cache: "no-store" }
  );

  const data = await res.json().catch(() => null);
  if (!res.ok) {
    return { error: data?.message || "Could not load payment info" };
  }

  return data;
}

export default async function PaymentSuccessPage({ params, searchParams }) {
  const { affiliate_id } = await params;

  const { search_params } = await searchParams;

  if (!affiliate_id) {
    return (
      <main className="container py-5">
        <div className="alert alert-danger">Invalid affiliate id in URL.</div>
        <Link href="/" className="btn btn-dark mt-3">Go Home</Link>
      </main>
    );
  }

  const redirectStatus = search_params?.redirect_status; // succeeded/failed
  let paymentIntent = search_params?.payment_intent || "";

  const info = await getSuccessInfo(affiliate_id);

  if (info?.error) {
    return (
      <main className="container py-5">
        <div className="alert alert-danger">{info.error}</div>
        <Link href={`/register/payment/${affiliate_id}`} className="btn btn-primary mt-3">
          Retry Payment
        </Link>
      </main>
    );
  }

  // If Stripe redirect says failed
  if (redirectStatus && redirectStatus !== "succeeded") {
    return (
      <main className="container py-5">
        <div className="card border-0 shadow-sm">
          <div className="card-body p-4 p-lg-5">
            <div className="d-flex align-items-center gap-3 mb-3">
              <div
                className="rounded-circle d-inline-flex align-items-center justify-content-center"
                style={{ width: 44, height: 44, background: "#fee2e2" }}
              >
                <i className="fa-solid fa-circle-xmark text-danger fs-4"></i>
              </div>
              <div>
                <h2 className="mb-0 fw-bold">Payment not completed</h2>
                <div className="text-muted">
                  Your payment did not go through. Please try again.
                </div>
              </div>
            </div>

            <div className="d-flex flex-wrap gap-2">
              <Link href={`/register/payment/${affiliate_id}`} className="btn btn-primary">
                Retry Payment
              </Link>
              <Link href="/" className="btn btn-outline-secondary">
                Go Home
              </Link>
            </div>
          </div>
        </div>
      </main>
    );
  }

  // console.log("Payment success info:", info);

  const aff = info.affiliate;
  const pay = info.latest_payment;

  // console.log(aff);
  // console.log(pay);

  const start_date = formatDate(aff.start_date);   // MM/DD/YYYY
  const end_date   = formatDate(aff.end_date);

  if(!paymentIntent) {
    paymentIntent = aff.stripe_payment_intent_id;
  }

  return (
    <main className="container py-5">
      <div className="row justify-content-center">
        <div className="col-lg-8">
          <div className="card border-0 shadow-sm">
            <div className="card-body p-4 p-lg-5">
              <div className="d-flex align-items-center gap-3 mb-3">
                <div
                  className="rounded-circle d-inline-flex align-items-center justify-content-center"
                  style={{ width: 44, height: 44, background: "#dcfce7" }}
                >
                  <i className="fa-solid fa-circle-check text-success fs-4"></i>
                </div>

                <div>
                  <h1 className="mb-0 fw-bold">Payment Successful</h1>
                  <div className="text-muted">Your subscription is active now.</div>
                </div>
              </div>

              <div className="rounded-3 p-3 p-lg-4 mb-4 bg-light">
                <div className="row g-3">
                  <div className="col-md-6">
                    <div className="text-muted small">Store</div>
                    <div className="fw-semibold">
                      {aff.store_name || aff.business_name || `Affiliate #${aff.affiliate_id}`}
                    </div>
                  </div>

                  <div className="col-md-6">
                    <div className="text-muted small">Email</div>
                    <div className="fw-semibold">{aff.email}</div>
                  </div>

                  <div className="col-md-6">
                    <div className="text-muted small">Amount</div>
                    <div className="fw-semibold">{pay ? money(pay.amount) : "-"}</div>
                  </div>

                  <div className="col-md-6">
                    <div className="text-muted small">Invoice</div>
                    <div className="fw-semibold">{pay?.invoice_number || "-"}</div>
                  </div>

                  <div className="col-md-6">
                    <div className="text-muted small">Billing Period</div>
                    <div className="fw-semibold">
                      {pay?.start_date ? String(start_date) : "-"} â†’{" "}
                      {pay?.end_date ? String(end_date) : "-"}
                    </div>
                  </div>

                  <div className="col-md-6">
                    <div className="text-muted small">Payment Intent</div>
                    <div className="fw-semibold">{paymentIntent || "-"}</div>
                  </div>
                </div>
              </div>

              <div className="d-flex flex-wrap gap-2">
                <Link href="/dashboard" className="btn btn-dark btn-lg">Go to Dashboard</Link>
                <Link href="/" className="btn btn-outline-secondary btn-lg">Back to Home</Link>
              </div>
            </div>
          </div>

          <div className="text-center text-muted small mt-3">Need help? Contact support.</div>
        </div>
      </div>
    </main>
  );
}